import { createIMG } from "./Image";

export const createAvatar = ({hasCaption = false, url = '/avatar-michelle.jpg', alt = "michelle", name = "Michelle Appleton", date = "28 Jun 2020"}) => {
  const avatar = document.createElement('div');
  const avatarImgageWrapper = document.createElement('figure');
  const avatarImage = createIMG({url, alt});
  const avatarCaption = `<figcaption class="avatar-caption"><strong>${name}</strong><span>${date}</span></figcaption>`;

  avatar.className = 'avatar';
  avatarImgageWrapper.className = 'avatar-thumbnail';

  avatarImgageWrapper.appendChild(avatarImage);
  avatar.appendChild(avatarImgageWrapper);

  hasCaption && avatar.insertAdjacentHTML('afterend', avatarCaption);

  console.log(avatarCaption);

  return avatar;
};
